.error {
  display: none;
  font-size: 22px;
  color: #ff3d00;
}
.loader {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  border: 2px solid #ff3d00;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.loader::after {
  content: '';
  box-sizing: border-box;
  position: absolute;
  left: 4px;
  top: 4px;
  border: 2px solid #000;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
img {
  max-width: 800px;
  max-height: 500px;
  width: 100%;
  height: 100%;
}
.breed-select {
  margin-bottom: 30px;
}
p {
  max-width: 800px;
}
.cat-info {
  display: flex;
}
